<script>
import i18n from './i18n';
import HelloI18n from './components/HelloI18n'

export default {
  name: 'App',
  data() {   
    return {        
      title: 'Yoga Sutra',
      localeFont: 'sanskrit',
      languages: [            
      { 
        flag: 'us', 
        language: 'en', 
        title: 'English' 
      },            

      { 
        flag: 'in', 
        language: 'sa', 
        title: 'Sanskrit' 
      },            
      { 
        flag: 'de', 
        language: 'de', 
        title: '日本語' 
      },            
      { 
        flag: 'cn', 
        language: 'cn', 
        title: '中文' 
      },            
      { 
        flag: 'cz', 
        language: 'cz', 
        title: 'Czech' 
      },            
      { 
