      },            
      { 
        flag: 'es', 
        language: 'es', 
        title: 'Español' 
      }        
      ]    
   };},
 components: {
    HelloI18n
  },
  methods: {
    changeLocale(locale){
      console.log("changing to " + locale);
      i18n.locale = locale;

      if ( locale == "sa"){
        this.localeFont = 'sanskrit';
      } else if ( locale == "en"){
        this.localeFont = 'indic';
      }
    }
  }
}
</script>

